	module.exports = {"data":{"markdownRemark":{"html":"<p>Earlier this week, <a href=\"http://www.google.com/doodles/bartolomeo-cristoforis-360th-birthday\">Google released an interactive doodle celebrating the inventor of the piano, Bartolomeo Cristoforo</a>. I loved this doodle as soon as a I saw it. Though it’s a pretty simple interaction, there’s quite a bit of subtlety to it.</p>\n<h2>Forte to Piano Slider</h2>\n<p>The Forte-Piano slider caught my eye immediately because it’s something that I can be easily implemented in several elearning authoring tools, and is perhaps especially easy in Storyline 2. The slider is also interesting because it’s the only interaction you have with the piece: making it loud or soft.</p>\n<h2>Animation</h2>\n<p>But don’t be fooled by the simple interface. The animation that it controls is super interesting. So on piano, little animated Bartolomeo sits in his seat, quietly playing his piano.</p>\n<p>We are also treated to a top-down view of the keyboard that shows which keys he plays, and an inside view of the piano, showing the hammer (relatively) softly hitting the wire.</p>\n<figure>\n  <img src=\"/bartsoft-eaf736323221f07bd7785bc5a9477b8a.gif\" alt=\"playing the piano softly\">\n</figure>\n<p>As we move towards forte, Bartolomeo gets more active in his playing (did you notice the subtle gradations between the two extremes?) and we can see that the hammer hits the keys harder.</p>\n<figure>\n  <img src=\"/bartloud-7b9f568b53ec3be4b2cb1ab70adcaa4f.gif\" alt=\"playing the piano loudly\">\n</figure>\n<p>How do we know? The size and shape of the sound lines emanating from the hammer and wire. I loved the subtlety there. We can hear the notes getting louder, but we’re also given fantastic visual cues that Bartolomeo is hitting the keys harder (at forte he is actually bouncing up and down in the seat in a whole body movement, as opposed to piano where his eyebrows move to the beat) and that the hammer is hitting the wire harder. All of this without a single line of text or a voice over. It makes me thing about how often we justify instruction text and voice over with the idea that our users just won’t get it otherwise. Here’s a doodle with truly international reach that illustrated a concept without either of those things. If nothing else, we should file it as inspiration that it can be done!</p>\n<h2>Future Project?</h2>\n<p>The clever use of animation may be difficult to duplicate but, on the whole, this is fairly simple. I’d like to try an emulate the design in Storyline in the near future and I’ve love to see what you all come up with. Any takers? Let me see you’re design in the comments.</p>","frontmatter":{"date":"May 07, 2015","path":"/blog/interaction-inspiration-google-doodle-for-bartolomeo-cristoforo","title":"Interaction Inspiration: Google Doodle for Bartolomeo Cristoforo","tags":["doodle","google","google doodle","inspiration","music","slider"],"categories":["Design"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.2185567010309277,"src":"/static/bartolomewgoogledoodle-0724085248f22bff134b298bc3cfe6fc-0ef27.png","srcSet":"/static/bartolomewgoogledoodle-0724085248f22bff134b298bc3cfe6fc-3c734.png 203w,\n/static/bartolomewgoogledoodle-0724085248f22bff134b298bc3cfe6fc-c3771.png 405w,\n/static/bartolomewgoogledoodle-0724085248f22bff134b298bc3cfe6fc-0ef27.png 591w","sizes":"(max-width: 591px) 100vw, 591px"}}}}}},"pathContext":{}}

